@import "src/styles/global.scss";
.vendors {
  padding-bottom: 100px;

  &__header {
    font-family: $font-family;
    color: #fbf6f3;
    background-image: url('../../assets/images/vendors.webp');
    background-position: center;
    background-size: cover;
    padding-block: 95px 112px;
  }

  &__title {
    margin-bottom: 47px;
    font-family: $font-family;
    font-weight: 700;
    font-size: 56px;
    line-height: 68px;
    color: #fbf6f3;
  }

  &__subtitle {
    margin-bottom: 37px;
    font-weight: 700;
    font-size: 36px;
    line-height: 44px;
  }

  &__text {
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
  }

  &__content-title {
    font-family: $font-family;
    font-weight: 700;
    font-size: 36px;
    line-height: 44px;
    text-transform: uppercase;
    color: $color-primary;
    padding-block: 64px 56px;
  }

  &__form {
    @include page-grid;

    border-radius: 4px;
    padding: 32px 22px;
    background-color: $color-bg-accent;
    row-gap: 32px;
  }

  &__form-group {
    display: flex;
    flex-direction: column;

    &--contacts {
      grid-column: 1 / 7;
    }

    &--about {
      grid-column: 8 / -1;
    }
  }

  &__fields-group {
    display: flex;
    flex-direction: column;
    gap: 32px;
  }

  &__form-title {
    margin-bottom: 48px;
    font-family: $font-family;
    font-weight: 500;
    font-size: 24px;
    line-height: 29px;
    color: $color-text;
  }

  &__form-field {
    display: flex;
    flex-direction: column;
    gap: 24px;
  }

  &__form-text {
    width: -moz-fit-content;
    width: fit-content;
    font-family: $font-family;
    font-weight: 700;
    font-size: 16px;
    line-height: 20px;
    color: $color-text;
  }

  &__star {
    color: #b66233;
  }

  &__dropdown {
    padding: 12px 16px;
  }

  &__dropdown-content {
    overflow-y: scroll;
    padding-top: 4px;
    max-height: 380px;

    &::-webkit-scrollbar {
      width: 10px;
    }

    &::-webkit-scrollbar-track {
      width: 6px;
      background-color: #ecddd5;
    }

    &::-webkit-scrollbar-thumb {
      border-radius: 4px;
      background-color: $color-primary;
    }
  }

  &__checkboxes {
    display: flex;
    flex-wrap: wrap;
    gap: 8px;
  }

  &__form-textarea {
    border: 1px solid #d3c5be;
    border-radius: 4px;
    padding: 16px 32px;
    height: 56px;
    max-width: 496px;
    font-family: $font-family;
    font-weight: 500;
    font-size: 16px;
    line-height: 20px;
    color: $color-text;
    background-color: transparent;
    transition: border $effectDuration;
    resize: vertical;

    &::-moz-placeholder {
      font-family: $font-family;
      font-weight: 500;
      font-size: 16px;
      color: #d3c5be;
    }

    &::placeholder {
      font-family: $font-family;
      font-weight: 500;
      font-size: 16px;
      color: #d3c5be;
    }

    &:focus {
      outline: none;
      border: 1px solid $color-primary;
    }
  }

  &__error-message {
    grid-column: 9 / -1;
    font-family: $font-family;
    font-weight: 500;
    font-size: 14px;
    color: $color-primary;
  }

  &__btn {
    grid-column: 9 / -1;
    border: 1px solid $color-primary;
    border-radius: 4px;
    font-family: $font-family;
    font-weight: 700;
    font-size: 16px;
    text-align: center;
    color: $color-bg-accent;
    background-color: $color-primary;
    cursor: pointer;
    padding-block: 15px;
  }
}
